:root {
  --navs-color: #fff;
  /* --main-bg: rgb(252, 224, 0); */
  /* --main-bg: #f4fe81; */
  --main-bg: #69f0ae;
  --shopping-card: #000000;
  --mobile-nav: #2c2c2c;
  --card-span: #000;
  --fixed-card: #fde61d;
  --main-color: #ffffff;
  --product-bg: ##ffffff;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  background: #ffffff;
  font-family: "Roboto", sans-serif;
}

button {
  font-family: "Roboto", sans-serif;
  cursor: pointer;
}

/* HEADER START */

header {
  position: relative;
  font-size: 16px;
  margin-bottom: 120px;
}

.wrapper {
  max-width: 1130px;
  margin: auto;
}

.header__logo {
  display: inline-block;
  max-width: 200px;
  font-weight: 700;
}

.header__logo__img {
  width: 200px;
}

.header__logo__mobile {
  display: none;
}

.logo__span {
  text-transform: uppercase;
}

.header__call {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__btn {
  display: none;
  background-color: transparent;
  border: none;
  font-size: 25px;
}

.header__menu__box {
  display: none;
}

.our__social {
  display: flex;
  flex-direction: column;
}

.header__call__link {
  color: #2c2c2c;
  font-size: 18px;
}

.header__call__text {
  color: #7b7b7b !important;
  font-size: 14px;
}

i.fa-headset {
  opacity: 0.4;
}

.header__color {
  width: 100%;
  background-color: var(--navs-color);
  position: fixed;
  border-bottom: 1px solid #dedede;
  top: 0;
  z-index: 999;
}

/* .menu__fixed {
  position: fixed !important;
  z-index: 999;
  top: 0;
  left: 0;
} */

.header__bottom {
  display: flex;
  padding: 15px 0;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

i.fa-bars {
  color: #fff;
  font-size: 25px;
}

.header__form {
  display: flex;
  background-color: #ffffff;
  height: 42px;
  position: relative;
  align-items: center;
  max-width: 315px;
  width: 90%;
  justify-content: space-between;
  border: 1px solid #e6e6e6;
  border-radius: 8px;
}

.header__search {
  width: 100%;
  height: 100%;
  border: none;
  margin-left: 15px;
  font-size: 18px;
  color: #a1a1a1;
}

.search__btn {
  height: 100%;
  padding-right: 15px;
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #000;
}

.header__action {
  display: flex;
  align-items: center;
}

.user__list {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  position: absolute;
  display: none;
  opacity: 1;
  top: 90px !important;
  left: inherit !important;
  right: 0px;
  overflow: visible;
  width: 280px !important;
  z-index: 999;
  background-color: #fff;
  margin: 0;
  min-width: 100px;
  max-height: 650px;
  cursor: pointer;
}

.user__list__item {
  color: rgba(0, 0, 0, 0.87);
  cursor: pointer;
  min-height: 50px;
  line-height: 1.5rem;
  width: 100%;
  text-align: left;
  text-transform: none;
  border-bottom: 1px solid #eee;
}

.user__list__link {
  color: #2c2c2c;
  text-decoration: none;
  padding: 13px 17px;
  font-size: 16px;
  display: block;
  line-height: 22px;
}

.user__list__link i {
  font-size: 22px;
  margin-right: 21px;
}

.header__action__item {
  margin-right: 20px;
  font-size: 20px;
  color: #000000;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  background-color: transparent;
  justify-content: center;
  border-radius: 50%;
  transition: ease-in-out 0.3s;
  border: 1px solid #e6e6e6;
}

.header__shopping {
  display: flex;
  align-items: center;
}

.shopping__card {
  font-size: 35px;
  color: var(--shopping-card);
  position: relative;
  font-weight: 700;
}

.card__span {
  font-size: 14px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0%;
  left: 40%;
  color: #ffffff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: var(--card-span);
  text-align: center;
}

.shopping__wallet {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 15px;
  width: 115px;
  align-items: center;
}

.shopping__amount {
  font-weight: 700;
  font-size: 17px;
  color: #2c2c2c;
}

.shopping__sale {
  font-size: 13px;
  color: #2c2c2c;
  font-weight: 500;
}

.fixed__card {
  position: fixed;
  bottom: 10px;
  width: calc(100% - 20px);
  z-index: 9997;
  height: 50px;
  right: 10px;
  left: 10px;
  background: var(--fixed-card);
  border-radius: 10px;
  display: none;
}

.fixed__card__link {
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  position: relative;
  align-items: center;
  font-size: 16px;
  padding: 0 10px;
  color: #262626 !important;
}

.fixed__card__count {
  position: unset !important;
}

.fixed__card__count,
.fixed__card__icon,
.fixed__card__amount {
  display: block;
}

.fixed__card__icon {
  position: absolute;
  right: 50%;
  transform: translateX(50%);
}

/* HEADER END */

/* FORM SECTION START */
.form {
  display: none;
}

.form__mobile {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  border-radius: 10px;
  position: relative;
}

.form__search {
  width: 100%;
  height: 40px;
  border-radius: 10px;
  background-color: transparent;
  border: 1px solid #ced4da;
  font-size: 16px;
  padding-left: 10px;
  color: #a1a1a1;
}

.form__submit {
  position: absolute;
  height: 40px;
  width: 40px;
  right: 0;
  border: none;
  background-color: transparent;
  font-size: 20px;
  z-index: 99;
  border-end-end-radius: 10px;
  border-end-start-radius: 10px;
  color: var(--main-color);
}

/* FORM SECTION START */

/* SECTION SLIDER START */

.slider {
  margin-top: 20px;
}

.slider__head .owl-nav {
  text-align: center;
}

.slider__head .owl-prev,
.slider__head .owl-next {
  width: 45px;
  height: 45px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #535252;
}

.slider__head .owl-prev {
  position: absolute;
  left: -2%;
  top: 45%;
}

.slider__head .owl-next {
  position: absolute;
  right: -2%;
  top: 45%;
}

.slider__head .owl-dots {
  position: absolute;
  bottom: 2%;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  left: 0;
}

.slider__head .owl-dot {
  display: block;
  background-color: white;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  margin: 0 5px;
}

.slider__head .owl-dot.active {
  background-color: #7ac143;
}

/* SECTION SLIDER END */

/* CATEGORIES START */
.categories {
  margin-top: 35px;
}

.categories__img {
  width: unset !important;
}

.categories .owl-prev,
.categories .owl-next {
  position: absolute;
  top: 25%;
  width: 45px;
  height: 45px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #535252;
  border-radius: 50%;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.1);
}

.categories .owl-next {
  right: -2%;
}

.categories .owl-prev {
  left: -2%;
}

.categories__head {
  text-align: center;
  font-weight: 400;
  font-size: 22px;
  color: #555;
  margin-bottom: 20px;
}

.categories__box__big {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.categories__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #2c2c2c;
  font-weight: 400;
  font-size: 16px;
}

.categories__box__link {
  margin: 15px;
}

.categories__item {
  display: flex;
  width: 130px;
  height: 130px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 10px;
  border: 1px solid #e6e6e6;
  transition: ease-in-out 0.3s;
}

.categories__item:hover {
  background-color: #fde61d;
}

/* CATEGORIES END */

/* SECTION PRODUCTS START */
.products {
  margin-top: 30px;
  margin-bottom: 50px;
}

.products__head {
  margin-top: 25px;
}

.products__slider {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.products__link {
  font-weight: 400;
  font-size: 22px;
  display: inline-block;
  color: #555;
  margin-bottom: 25px;
}

.products__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 250px;
  height: 340px;
  background-color: #fff;
  border: 1px solid #bebdbd;
  border-radius: 7px;
  padding: 15px;
}

.products__item__link {
  border-bottom: 1px solid #e7e7e7;
  max-width: 215px;
  height: 160px;
  overflow: hidden;
  text-align: center;
  background-color: var(--product-bg);
  display: flex;
  justify-content: center;
}

.owl-carousel .owl-item img {
  max-height: 100%;
}

.products__item__name {
  font-size: 16px;
  font-weight: 400;
  margin: 10px 0;
  color: rgb(29, 26, 26);
}

.products__item__amount {
  display: block;
  font-size: 20px;
  color: rgb(29, 26, 26);
  font-weight: 500;
  margin-bottom: 15px;
}

.products__item__btns {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
}

.admin__pro__btn{
  justify-content: space-between;
}

.products__item__btn {
  background-color: var(--main-bg);
  color: var(--main-color);
  display: flex;
  align-items: center;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
  transition: ease-in 0.3s;
  width: 100%;
  justify-content: center;
}

.products__item__icon {
  font-size: 20px;
  margin-left: 15px;
}

.quantity {
  display: none;
  font-size: 25px;
  align-items: center;
  text-align: center;
  height: 44px;
}

.quantity__minus,
.quantity__pilus,
.quantity__add {
  width: 30px;
  height: 30px;
  border: 2px solid var(--main-bg);
  color: rgb(29, 26, 26);
  background-color: transparent;
}

.quantity__pilus {
  background-color: var(--main-bg);
  color: var(--main-color);
}

.quantity__show {
  display: block;
  width: 35px;
  text-align: center;
}

.quantity__add {
  background-color: transparent;
  margin-left: 90px;
  color: #b42715;
  font-size: 25px;
  font-weight: 700;
  background-color: transparent;
  border: none;
}

.products__item__chosen {
  font-size: 25px;
  background-color: #fff;
  color: var(--main-bg);
  display: none;
}

.products__slider .owl-nav {
  text-align: center;
}

.products__slider .owl-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 40px;
  height: 40px;
  left: 0;
  top: 45%;
  background-color: var(--main-bg);
  color: var(--main-color);
  border-radius: 50%;
}

.products__slider .owl-next {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 40px;
  height: 40px;
  right: 0;
  top: 45%;
  background-color: var(--main-bg);
  color: var(--main-color);
  border-radius: 50%;
}

/* SECTION PRODUCTS END */

/* SECTION FEATURES START */
.features {
  margin-bottom: 50px;
}

.features__head {
  font-size: 35px;
  text-align: center;
  color: var(--main-bg);
  font-weight: 700;
  margin-bottom: 25px;
}

.features__box {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.features__item {
  width: 345px;
  padding: 10px;
  margin: 0 auto;
  border: 1px solid var(--main-bg);
  border-radius: 7px;
  height: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.features__link {
  display: block;
  color: var(--main-bg);
  font-size: 75px;
  margin-top: 20px;
}

.features__name {
  font-size: 25px;
  margin-top: 15px;
  color: rgb(29, 26, 26);
}

.features__text {
  font-size: 16px;
  margin-top: 15px;
  color: rgb(29, 26, 26);
}

/* SECTION FEATURES END */

/* FOOTER START */
footer {
  background-color: #2c2c2c;
  padding: 30px 0;
}

.footer__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.footer__list {
  display: flex;
  align-items: center;
}

.footer__list__item {
  margin-right: 20px;
  font-size: 14px;
  color: white;
  line-height: 22px;
}

.footer__list__link {
  color: white;
}

.footer__block,
.footer__social {
  display: flex;
  align-items: center;
}

.footer__social__item {
  margin-left: 15px;
  line-height: 32px;
}

.footer__social__link {
  display: block;
  padding-left: 10px;
  width: 100%;
  height: 100%;
  font-size: 20px;
  color: white;
}

.footer__copyrighting {
  margin-left: 70px;
  color: white;
  font-size: 14px;
  text-transform: uppercase;
}

.scrollToTop {
  position: fixed;
  display: none;
  font-size: 30px;
  align-items: center;
  justify-content: center;
  bottom: 5%;
  right: 5%;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  z-index: 888;
  border: none;
  background-color: var(--main-bg);
  color: var(--main-color);
}

/* FOOTER END */

/* SHOPPING CARD PAGE START */

/* SECTION CARD START */
.card {
  padding: 50px 0;
}

.card__head {
  text-align: start;
  font-size: 25px;
  color: var(--main-bg);
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: start;
  border-bottom: 1px solid #bebdbd;
  padding-bottom: 20px;
}

.card__head__count {
  display: flex;
  width: 40px;
  height: 35px;
  margin-left: 15px;
  align-items: center;
  justify-content: center;
  background-color: var(--main-bg);
  color: var(--main-color);
  border-radius: 40px;
  font-style: 23px;
}

.card__box {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
}

.card__products {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.card__product {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f4f4f4;
  border-right: 1px solid #f4f4f4;
  padding: 10px 0;
}

.card__product__link {
  width: 170px;
  height: 150px;
}

.card__product__img {
  width: 100%;
  height: 100%;
}

.card__about {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: rgb(29, 26, 26);
}

.card__about__name {
  font-size: 20px;
  margin-bottom: 15px;
}

.card__about__amount {
  font-size: 16px;
}

.card__action {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  color: rgb(29, 26, 26);
}

.card__action__box {
  display: flex;
  align-items: center;
  font-size: 20px;
  margin-bottom: 10px;
}

.card__action__amount {
  font-weight: 700;
  margin-right: 5px;
}

.card__btns {
  display: flex;
  align-items: center;
}

.card__minus,
.card__show,
.card__pilus {
  display: block;
  width: 30px;
  height: 30px;
  font-size: 16px;
}

.card__minus {
  border: 2px solid var(--main-bg);
}

.card__show {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 500;
}

.card__pilus {
  border: 2px solid var(--main-bg);
  background-color: var(--main-bg);
  color: var(--main-color);
}

.card__product__remove,
.card__product__check {
  margin-left: 30px;
  width: 30px;
  height: 30px;
  font-size: 18px;
  border: none;
  background-color: #fff;
  color: rgb(156, 22, 22);
}

.card__product__check {
  color: var(--main-bg);
  display: none;
}

.card__about__img {
  display: none;
}

.card__payment {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  padding: 20px;
  position: sticky;
  top: 10%;
  padding-bottom: 10px;
}

.payment__top {
  display: none;
  align-items: center;
  padding: 10px;
  background-color: #ffffcc;
}

.payment__top__item {
  font-size: 20px;
  color: rgb(180, 11, 11);
  margin-right: 10px;
}

.payment__top__text {
  text-align: center;
  font-size: 14px;
  color: rgb(29, 26, 26);
}

.payment__bottom {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.payment__basket {
  display: flex;
  align-items: flex-start;
  margin-bottom: 15px;
}

.payment__view__span {
  font-size: 25px;
  color: rgb(29, 26, 26);
}

.payment__total {
  display: flex;
  font-size: 20px;
  align-items: flex-end;
}

.payment__total__amount {
  font-size: 25px;
  margin-right: 5px;
  font-weight: 700;
}

.payment__view__item {
  font-size: 15px;
  color: #b2b2b2;
  font-weight: 400;
  width: 75%;
}

.payment__sale {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.payment__sale__span {
  font-size: 20px;
  font-weight: 400;
  color: rgb(29, 26, 26);
  font-weight: 500;
  letter-spacing: 1.3px;
}

.payment__sale__box {
  color: rgb(180, 11, 11);
  font-weight: 500;
}

.payment__data {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  color: #9ea1a7;
  margin: 0 auto;
  margin-top: 15px;
  font-size: 1rem;
  width: max-content;
  /* display: none; */
}

.data__text {
  color: #ef404a;
}

.data__items {
  height: 44px;
  width: 200px;
  border: 1px solid #e4e4e4;
  border-radius: 3px;
  padding: 10px 20px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
}

.data__items .fa-chevron-down {
  margin-left: 10px;
}

.data__list {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  top: 100%;
  color: #000000;
  background-color: #fff;
  border: 1px solid #ef404a;
  border-radius: 3px;
  width: 100%;
  z-index: 99;
}

.data__list__item {
  padding: 10px 10px;
  cursor: pointer;
  border-bottom: 1px solid #e4e4e4;
}

.data__list__item:last-child {
  border: none;
}

.payment__btn {
  margin-top: 25px;
  text-align: center;
  width: 100%;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 3px;
  border: none;
  background: var(--main-bg);
  color: var(--main-color);
  display: none;
}

#showButton {
  margin-top: 25px;
  padding: 10px 20px;
  width: 100%;
  background: var(--main-bg);
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  border-radius: 3px;
  border: none;
  color: var(--main-color);
}

#instruction {
  color: #ef404a;
  text-align: center;
  margin: 20px 0;
}

#hideButton {
  width: 100%;
  padding: 10px 20px;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  background-color: #ef404a;
  color: #fff;
  margin-top: 20px;
  border: none;
}

.card__products__add {
  align-self: flex-end;
  padding: 10px 20px;
  font-size: 18px;
