/* SECTION MODAL START */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.15s linear;
  overflow: hidden;
}

.modal__view {
  background-color: #fff;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
  border-radius: 0.125rem;
  max-width: 500px;
  margin: 1.75rem auto;
  transform: translateY(-1000px);
  overflow: visible;
  transition: transform 0.3s ease-out;
  margin-top: 10%;
  z-index: 1001;
  position: relative;
}

.modal__tabs {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  margin: -1.5rem 1rem 0;
  display: flex;
  background-color: var(--main-bg);
  padding: 0.7rem;
  z-index: 1;
  position: relative;
  border-radius: 0.25rem;
  transform: translateY(-50%);
  align-items: center;
}

.modal__tabs__item {
  width: 50%;
}

.modal__tabs__link {
  display: block;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #fff;
  text-align: center;
  line-height: 1.5;
}

.modal__tabs__link.active {
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
  transition: all 1s;
  border-radius: 0.25rem;
}

.modal__content {
  padding: 1.7rem 0 0;
}

.modal__content > .modal__panel {
  display: none;
  opacity: 0;
  transition: opacity 0.15s linear;
}

.modal__content > .active {
  display: block;
  opacity: 1;
}

.modal__body {
  padding: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  color: #616161;
  margin-bottom: 0.25rem;
  position: relative;
  text-align: center;
}

.modal__form {
  position: relative;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  color: #616161;
  text-align: start;
  display: flex;
  flex-direction: column;
}

.modal__input {
  display: block;
  color: #6c757d;
  border: 1px solid #ced4da;
  border-radius: 5px;
  background-color: #e9eff6;
  margin: 0 0 0.5rem;
  padding: 12px 0;
  font-size: 1rem;
  line-height: 1.5;
  padding-left: 15px;
}

.modal__order__btn,
.modal__login__btn {
  background-color: var(--main-bg);
  color: var(--main-color);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  padding: 12px 20px;
  border: none;
  font-size: 1rem;
  margin: 5px;
  border-radius: 5px;
  line-height: 1.5;
  text-transform: uppercase;
  margin-top: 15px;
}

.modal__footer {
  color: #616161;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  border-top: 1px solid #e9ecef;
  border-bottom-right-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;
  padding-bottom: 25px;
}

.modal__footer__options {
  margin-right: 0.25rem;
