.goods__checks__checkbox {
  -webkit-appearance: none;
  visibility: hidden;
  display: none;
}

.goods__checks__span {
  position: relative;
  display: block;
  width: 40px;
  height: 22px;
  border: 1px solid #092c3e;
  background: #092c3e;
  cursor: pointer;
  border-radius: 20px;
  overflow: hidden;
  transition: ease-in 0.5s;
  margin-bottom: 20px;
}

.goods__checks__checkbox:checked ~ .goods__checks__span {
  background: #fff;
}

.goods__checks__span::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  background-color: #fff;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transition: 0.5s;
}

.goods__checks__checkbox:checked ~ .goods__checks__span::before {
  transform: translateX(50px);
}

.goods__checks__span::after {
  content: "";
  position: absolute;
  top: 2px;
  right: 2px;
  background-color: #092c3e;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transition: 0.5s;
  transform: translateX(50px);
}

.goods__checks__checkbox:checked ~ .goods__checks__span::after {
  transform: translateX(0px);
}

